import react from 'react'


function RecipeCard(props) {
        return (
            <div className="container card cardy">
                <div className="card-body">
                    <h5 className="card-title">{props.recipe.title}</h5>
                    <h6>{props.recipe.description}</h6>
                    <p className="card-text">{props.recipe.link}</p>
                    <btn className="card-link btn btn-danger">Explore!</btn>
                </div>
            </div>
        )
}

export default RecipeCard