import react, {Component} from 'react';
import { Link } from 'react-router-dom';

export default class Navbar extends Component {
    render(){
        return(
            <nav id="nav" className="navbar navbar-expand-lg navbar-dark bg-dark">
                <a className="navbar-brand" href="#">Restaurant</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item active">
                            <Link to="/" className="nav-link" href="#">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/create" className="nav-link" href="#">Create recipe</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/listing" className="nav-link" href="#">Listing</Link>
                        </li>
                        
                    </ul>
                </div>
            </nav>
        )
    }
}