import react, {Component} from 'react';


const Descy = props => (

        <div class="input-group mb-3" id="listing">
            <input type="text" class="form-control" disabled value={props.ingredient} placeholder="Recipient's username" aria-label="Recipient's username" aria-describedby="button-addon2" />
            <div class="input-group-append">
                <button class="btn btn-danger" type="button" id="button-addon2">Delete</button>
            </div>
        </div>
    )




export default class CreateAndListIngredient extends Component{
    constructor(props){
        super(props);
        this.onChangeIngredient = this.onChangeIngredient.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
        this.state = {
            ingredientList: [] ,
            ingredient : ""
        }
    }
    onChangeIngredient(e){
        this.setState({
            ingredient: e.target.value
        })
    }
    onSubmit(e){
        e.preventDefault();
        this.setState({
            ingredientList: [...this.state.ingredientList, this.state.ingredient],
            ingredient: ""
        })
        this.state.ingredientList.map(item => {
            localStorage.setItem('ingredientList', this.state.ingredientList);
        })
        console.log(JSON.stringify(localStorage.getItem('ingredientList')))
    }

    listThem(){
        return this.state.ingredientList.map((item,i)=>{
            return <Descy ingredient={item} key={i} />
        });
    }


    render(){
        return(
            <div id="content">
                <div className="row">
                    <div class="col">
                        <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#exampleModal">
                            Add Ingredient
                        </button>
                    </div>
                    <div class="col-sm-5 offset-sm-2 col-md-6 offset-md-0">
                        {this.listThem()}
                    </div>
                </div>
                <div className="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <form onSubmit={this.onSubmit}>
                                <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalLabel">Add Ingredient: </h5>
                                </div>
                                <div className="modal-body">
                                    <div className="form-group">
                                        <input type="text" className="form-control" 
                                        onChange={this.onChangeIngredient} value={this.state.ingredient}
                                        />
                                    </div>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                    <button type="submit" className="btn btn-primary">Save changes</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}